<template>
  <div class="mainwrap">
    <section class="head">
      <template v-if="!editStatus">
        <div>
          <a @click="goBack" class="iconfont back"> &#xe614; </a>
          <a class="icon icon_manue" v-if="!cartEmpty"> manue </a>
        </div>
      </template>
      <template v-if="editStatus">
        <div class="headRadio" @click="refreshCheckAll">
          <span class="iconfont" :class="{ red: radioAllValue }">
            {{ radioAllValue ? "&#xe63a;" : "&#xe608;" }}
          </span>
        </div>
      </template>

      <h1>Cart</h1>
      <div v-if="cartEmpty">&nbsp;</div>
      <div class="edit" v-if="!cartEmpty" @click="editStatus = !editStatus">
        {{ editStatus ? "Cancel" : "Edit" }}
      </div>
    </section>

    <template v-if="!cartEmpty">
      <section class="cartList">
        <ul class="clearfix">
          <li v-for="(item, index) in initProData" :key="index">
            <van-swipe-cell :right-width="operateW">
              <div class="item">
                <div
                  class="radio"
                  @click="checkPro(index, item.id)"
                  :data-check="item.checked"
                  >
                    <span class="iconfont" :class="{ red: item.checked }">
                      {{ item.checked ? "&#xe63a;" : "&#xe608;" }}
                    </span>
                </div>
                <div class="imgWrap">
                  <img class="img" :src="item.settlementSku.img0" />
                  <!-- <span class="leftNum" :class="{'soldOut':!item.leftNum}"> {{!item.leftNum?"Sold out":(item.leftNum+"Left")}} </span> -->
                </div>
                <div class="rightMain">
                  <div class="name">{{ item.settlementSku.name }}</div>
                  <div class="cartDownWrap">
                    <div class="bottomPrice">
                      <p class="cartChooseSize">
                        <span> {{getAttributes(item.settlementSku.attributeList)}} </span>
                        <span class="iconfont"> &#xe671; </span>
                      </p>
                      <p class="red">{{ (item.settlementSku.mallPrice/100).toFixed(2) }} AED</p>
                      <!-- supplyPrice -->
                      <p class="gray">
                        {{ (item.settlementSku.price/100).toFixed(2) }} AED
                      </p>
                    </div>
                    <div class="downNum">
                      <template v-if="item.settlementSku.stock > 0">
                        <!--有库存 item.productSku.stock-->
                        <p class="free" v-if="item.settlementSku.isTypeFour==1">Free Shipping</p>
                        <div class="gw_num clearfix">
                          <em
                            class="jian iconfont"
                            @click="
                              jian(
                                item.id,
                                item.num,
                                item.settlementSku.stock,
                                item.skuCode
                              )
                            "
                          >
                            &#xe613;
                          </em>
                          <input
                            type="number"
                            v-model="item.num"
                            class="num"
                            @blur="
                              selectNum(
                                item.id,
                                item.num,
                                item.settlementSku.stock,
                                item.skuCode
                              )
                            "
                          />
                          <em
                            class="add iconfont"
                            @click="
                              jia(
                                item.id,
                                item.num,
                                item.settlementSku.stock,
                                item.skuCode
                              )
                            "
                          >
                            &#xe715;
                          </em>
                        </div>
                      </template>
                      <template v-if="item.settlementSku.stock == 0">
                        <!--售罄-->
                        <div class="findSame" @click="$router.push({path:'searchResult',query:{category2Code:''}})">Find Similarity</div>
                      </template>
                    </div>
                  </div>
                </div>
              </div>
              <div slot="right" class="slideOperate">
                <p class="share">
                  <span class="iconfont"> &#xe650; </span>
                  <span> Share </span>
                </p>
                <p class="like">
                  <span class="iconfont"> &#xe652; </span>
                  <span>
                    Move to <br />
                    Wishlist
                  </span>
                </p>
                <p class="del" @click="delPro(item.id)">
                  <span class="iconfont"> &#xe609; </span>
                  <span>Delete</span>
                </p>
              </div>
            </van-swipe-cell>
          </li>
        </ul>
      </section>

      <section class="salesWrap">
        <div class="box box-flex justify-between">
          <div class="timerWrap">
            <h3 class="homeTittle">Recommend for You</h3>
          </div>
          <div>
            <router-link :to="{ path: '/categoryElectronics', query: {} }">
              <a class="blue"> See All</a>
            </router-link>
          </div>
        </div>
        <div class="commonPro">
          <swiper :options="commonSwiperOption">
            <template v-for="(item, index) in recommendList" >
              <swiper-slide>
                <div class="item">
                  <div class="tips_cutOff" v-if="item.percentage">
                    {{ item.percentage }} OFF
                  </div>
                  <div
                    class="tips_love iconfont"
                    :class="{ active: item.love }"
                  >
                    {{ item.love ? "&#xe653;" : "&#xe652;" }}
                  </div>
                  <div class="tips_sale" v-if="item.sale"></div>
                  <img class="img" :src="item.spuMainImage" alt="" />
                </div>
                <div class="name">{{ item.name }}</div>
                <div class="bottomPrice">
                  <p class="red">{{ item.priceRange }} AED</p>
                  <p class="gray">{{ item.priceRangeLine }} AED</p>
                  <p v-if="item.isTypeFour==1">Free Shipping</p>
                </div>
                <div class="downShare">
                  <div :class="{ active: item.share }" @click="shareShow(item)">
                    <span class="share iconfont"> &#xe650; </span>
                  </div>
                  <div :class="{ active: item.cart }" @click="addChart(item)">
                    <span class="cart iconfont"> &#xe669; </span>
                  </div>
                </div>
              </swiper-slide>
            </template>
          </swiper>
        </div>

        <div class="box mt10 box-flex justify-between">
          <div class="timerWrap">
            <h3 class="homeTittle">Browse Record</h3>
          </div>
          <div>
            <router-link :to="{ path: '/browseRecord', query: {} }">
              <a class="blue"> See All</a>
            </router-link>
          </div>
        </div>
        <div class="commonPro">
          <swiper :options="commonSwiperOption">
            <template v-for="(item, index) in browserList">
              <swiper-slide>
                <div class="item">
                  <div class="tips_cutOff" v-if="item.percentage">
                    {{ item.percentage }} OFF
                  </div>
                  <div
                    class="tips_love iconfont"
                    :class="{ active: item.love }"
                  >
                    {{ item.love ? "&#xe653;" : "&#xe652;" }}
                  </div>
                  <div class="tips_sale" v-if="item.sale"></div>
                  <img class="img" :src="item.spuMainImage" alt="" />
                </div>
                <div class="name">{{ item.name }}</div>
                <div class="bottomPrice">
                  <p class="red">{{ item.priceRange }} AED</p>
                  <p class="gray">{{ item.priceRangeLine }} AED</p>
                  <p v-if="item.isTypeFour==1">Free Shipping</p>
                </div>
                <div class="downShare">
                  <div :class="{ active: item.share }" @click="shareShow(item)">
                    <span class="share iconfont"> &#xe650; </span>
                  </div>
                  <div :class="{ active: item.cart }" @click="addChart(item)">
                    <span class="cart iconfont"> &#xe669; </span>
                  </div>
                </div>
              </swiper-slide>
            </template>
          </swiper>
        </div>
      </section>

      <section v-if="!editStatus" class="downWrap">
        <div class="tips">
          <div class="red">{{cartData.message}}</div>
          <div class="gray" v-if="false">
            To gather up a list <span class="iconfont"> &#xe93d; </span>
          </div>
        </div>
        <div class="downBtn">
          <div class="leftAll" @click="refreshCheckAll">
            <span class="iconfont" :class="{ red: radioAllValue }">
              {{ radioAllValue ? "&#xe63a;" : "&#xe608;" }}
            </span>
            <span> All </span>
          </div>
          <div class="rightOperate">
            <span class="gray">Sub Total:</span>
            <span class="price"> {{ totalPrice }} AED </span>
            <van-button type="danger" @click="orderSummary">Check out({{selectSpuNum}})</van-button>
          </div>
        </div>
      </section>

      <section
        v-if="editStatus"
        class="editDownOperate"
        @click="editStatus = false"
      >
        <div class="col">
          <span class="iconfont"> &#xe652; </span>
          <span class="font10"> Move to Wishlist </span>
        </div>
        <div class="col">
          <span class="iconfont"> &#xe650; </span>
          <span class="font10">Share </span>
        </div>
        <div class="col">
          <span class="iconfont"> &#xe609; </span>
          <span class="font10"> Delete </span>
        </div>
      </section>
    </template>

    <template v-if="cartEmpty">
      <section class="cartEmpty">
        <img src="../common/images/cartEmpty.png" />
        <p class="gray">Your Cart is empty</p>
        <p class="btnCartWrap">
          <van-button type="danger" size="large" @click="$router.push('flashSale')">EXPLORE OUR DEALS</van-button>
        </p>
      </section>
    </template>
  </div>
</template>

<script>
import { myCarts, checkCart, deleteCart, updateCart } from "@/api/cart.js";
import {getUserBrowseProduct} from '@/api/user';
export default {
  name: "cart",
  data() {
    return {
      totalPreferentialPrice: null,
      totalPrice:"",
      cartEmpty: false,
      selectSpuNum:0,
      commonSwiperOption: {
        slidesPerView: 2.3,
        spaceBetween: 10,
        freeMode: true,
        pagination: {
          el: ".swiper-pagination",
          clickable: true
        }
      },
      radioAllValue: 0,
      operateW: 192,
      editStatus: false,
      cartList: [],
      initProData: [],
      recommendList:[],
      browserList:[],
      cartData:{}
  
    };
  },
  computed: {},
  mounted() {
    this.init();
  },
  methods: {
    init() {
      this.$store.dispatch("myCarts").then(res => {
        this.cartData=res.data.result;
        this.selectSpuNum=res.data.result.totalNum;
        this.initProData = res.data.result.cartProductList;
        this.recommendList = res.data.result.cartRecommendList;
        this.totalPreferentialPrice = res.data.result.totalPreferentialPrice;
        this.totalPrice = (res.data.result.totalPrice/100).toFixed(2);

        this.$store.dispatch('indexData',{lon:1,lan:1});
      });
      getUserBrowseProduct({pageNo:1,pageSize:8}).then(res=>{
         this.browserList=res.data.result;
      });
    },
    initCartCheck() {
      this.proDataBest.map(val => {
        this.initProData.push(
          JSON.parse(JSON.stringify(Object.assign(val, { check: true })))
        );
      });
    },
    //勾选商品
    checkPro(idx, id, checked) {
      this.initProData[idx].checked = !this.initProData[idx].checked;
      this.checkAll();
      if (checked) {
        checkCart(id).then(res => {

        });
      }
    },
    refreshCheckAll() {
      this.radioAllValue = !this.radioAllValue;
      this.initProData.map(val => {
        val.checked = this.radioAllValue;
      });
    },
    checkAll() {
      //检查是否全部选中
     let unCheckedNum=  this.initProData.filter(val => {
        return !val.checked;
      }).length;
      if (unCheckedNum > 0) {
        this.radioAllValue = 0;
      } else {
        this.radioAllValue = 1;
      }
      this.selectSpuNum=this.initProData.length-unCheckedNum;

    },
    // 删除商品
    delPro(id) {
      // this.initProData.splice(idx,1)
       this.$store.dispatch("deleteCart",{id}).then(res => {
          this.$toast("Delete Successfully");
          this.init();
      });
    },
    //修改商品
    jian(id, num, stock, skuCode) {
      num--;
      if (num <= 1) {
        num = 1;
      }
      this.$store.dispatch('updateCart',{id, num, skuCode}).then(res => {
          this.init();
      });
    },
    jia(id, num, stock, skuCode) {
      num++;
      if (num > stock) {
        this.$toast("Out of Stock");
        num = stock;
      }
     this.$store.dispatch('updateCart',{id, num, skuCode}).then(res => {
          this.init();
      });
    },
    selectNum(id, num, stock, skuCode) {
      if (num > stock) {
        this.$toast("Out of Stock");
        num = stock;
      }
      this.$store.dispatch('updateCart',{id, num, skuCode}).then(res => {
          this.init();
      });
    },
    goBack() {
      this.$router.back(-1);
    },
    //跳到确认订单页面
    orderSummary() {
      let ids = [];
      this.initProData.map(item => {
        if (item.checked && item.settlementSku.stock > 0) {
          ids.push(item.id)
        }
      });
      this.$router.push({
        path: "orderSummary",
        query: { ids: JSON.stringify(ids) }
      });
    },
     getAttributes(arr){
        return arr.map(t=>t.optionName).join(';')
    }
  }
};
</script>

<style lang="scss" scoped>
@import "../common/css/cart.scss";
  .commonPro .downShare{
    left: 0;
  }
</style>



